.coming_soon{
    position: relative;
    z-index: 1;
    &:after{
        position: absolute;
        content: "coming soon";
        left: 0;
        width: 100%;
        color: #415094;
        text-transform: uppercase;
        font-size: 12px;
        font-weight: 600;
        bottom: 20px;
    }
    &:hover{
        &:after{
            color: $white_color;
        }
    }
}

/**************** common css end ****************/